<template>
  <div>
    <h4 class="info-text">
      Brand details
    </h4>
    <div class="row justify-content-center mt-5">
      <div class="col-sm-5">
        <base-input
          name="brandName"
          required
          placeholder="Brand name"
          v-model="model.brandName"
          v-validate="modelValidations.brandName"
          :error="getError('brandName')"
          addon-left-icon="tim-icons icon-single-02"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <el-autocomplete>

        </el-autocomplete>
      </div>
      <div class="col-sm-5">
        <base-input
          name="email"
          required
          placeholder="Email"
          v-model="model.email"
          v-validate="modelValidations.email"
          :error="getError('email')"
          addon-left-icon="tim-icons icon-email-85"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <base-input
          name="username"
          required
          placeholder="Username"
          v-model="model.username"
          v-validate="modelValidations.username"
          :error="getError('username')"
          addon-left-icon="tim-icons icon-caps-small"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <base-input
          name="phone"
          required
          placeholder="Phone"
          v-model="model.phone"
          v-validate="modelValidations.phone"
          :error="getError('phone')"
          addon-left-icon="tim-icons icon-mobile"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <base-input
          name="headOfficePhone"
          placeholder="Head Office Phone"
          v-model="model.headOfficePhone"
          v-validate="modelValidations.headOfficePhone"
          :error="getError('headOfficePhone')"
          addon-left-icon="tim-icons icon-mobile"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <base-input
          name="headOfficeAddress"
          placeholder="Head Office Address"
          v-model="model.headOfficeAddress"
          v-validate="modelValidations.headOfficeAddress"
          :error="getError('headOfficeAddress')"
          addon-left-icon="tim-icons icon-email-85"
        >
        </base-input>
      </div>
      <div class="col-sm-5">
        <base-input
          name="crNumber"
          placeholder="CR Number"
          v-model="model.crNumber"
          v-validate="modelValidations.crNumber"
          :error="getError('crNumber')"
          addon-left-icon="tim-icons icon-email-85"
        >
        </base-input>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'brand-details',
  data() {
    return {
      model: {
        brandName: '',
        email: '',
        phone: '',
        username: '',
        headOfficePhone: '',
        headOfficeAddress: '',
        crNumber: ''
      },
      modelValidations: {
        brandName: {
          required: true,
          min: 3
        },
        email: {
          required: true,
          email: true
        },
        phone: {
          required: true,
          numeric: true
        },
        username: {
          required: true,
          min: 3
        },
        headOfficePhone: {
          required: true,
          numeric: true
        },
        headOfficeAddress: {
          required: true,
          min: 4
        },
        crNumber: {
          required: true,
          numeric: true
        }
      }
    }
  },
  methods: {
    getError(fieldName) {
      return this.errors.first(fieldName)
    },
    validate() {
      return this.$validator.validateAll().then(res => {
        this.$emit('on-validated', res, this.model)
        return res
      })
    }
  }
}
</script>
<style></style>
