<template>
    <div class="content">
        <div class="col-md-12 ml-auto mr-auto">
            <h2>SAMA Programs List</h2>
        </div>
        <div class="row mt-5">
            <div class="col-12">
                <card card-body-classes="table-full-width">
                    <h4 slot="header" class="card-title">List of Merchant Programs</h4>
                    <div>
                        <div class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap">
                            <el-select
                                class="select-primary mb-3 pagination-select"
                                v-model="pagination.perPage"
                                placeholder="Per page"
                            >
                                <el-option
                                    class="select-primary"
                                    v-for="item in pagination.perPageOptions"
                                    :key="item"
                                    :label="item"
                                    :value="item"
                                >
                                </el-option>
                            </el-select>
                            <base-input>
                                <el-input
                                    type="search"
                                    class="mb-3 search-input"
                                    clearable
                                    prefix-icon="el-icon-search"
                                    placeholder="Search records"
                                    v-model="searchQuery"
                                    aria-controls="datatables"
                                >
                                </el-input>
                            </base-input>
                        </div>
                        <div class="col-12 d-flex flex-row-reverse">
                            <base-button 
                                type="success"
                                 @click="modals.modal1 = true"
                                icon
                            >
                                <i class="tim-icons icon-simple-add"></i>
                            </base-button>
                            <modal :show.sync="modals.modal1"
                                body-classes="p-0"
                                modal-classes="modal-dialog-centered modal-md">
                              <card type="secondary"
                                    header-classes="bg-white pb-5"
                                    body-classes="px-lg-5 py-lg-5"
                                    class="border-0 mb-0">
                                  <template>
                                      <div class="text-center text-muted mb-4">
                                          <small>Or sign in with credentials</small>
                                      </div>
                                      <form role="form">
                                          <el-select class="select-danger mb-3"
                                                  placeholder="Select Type of Programs"
                                                  v-model="programTypeSelecion">
                                              <el-option v-for="option in selects.types"
                                                  class="select-danger"
                                                  :value="option.value"
                                                  :label="option.label"
                                                  :key="option.value">
                                              </el-option>
                                          </el-select>
                                          <base-input alternative
                                                      class="mb-3"
                                                      placeholder="Program Name"
                                                      addon-left-icon="ni ni-email-83">
                                          </base-input>
                                          <el-input alternative
                                              type="textarea"
                                              :rows="2"
                                              placeholder="Please input"
                                             >
                                          </el-input>
                                          <base-input alternative
                                                      type="textarea"
                                                      :rows="2"
                                                      placeholder="Terms and Condition"
                                                      autosize
                                                      addon-left-icon="ni ni-lock-circle-open">
                                          </base-input>
                                          <div>
                                            <image-upload @change="onImageChange" select-text="Select Image" />  
                                          </div>
                                          <div v-if="programTypeSelecion == 1">
                                              <base-input alternative
                                                        class="mb-3"
                                                        placeholder="Money Spent"
                                                        addon-left-icon="ni ni-email-83">
                                              </base-input>
                                              <base-input alternative
                                                        placeholder="Points Earned"
                                                        addon-left-icon="ni ni-lock-circle-open">
                                              </base-input>
                                          </div>
                                          <div v-if="programTypeSelecion == 2">
                                              <base-input alternative
                                                        class="mb-3"
                                                        placeholder="Price off"
                                                        addon-left-icon="ni ni-email-83">
                                              </base-input>
                                              <base-input alternative
                                                        placeholder="Percentage Off"
                                                        addon-left-icon="ni ni-lock-circle-open">
                                              </base-input>
                                          </div>
                                          <div class="text-center">
                                              <base-button type="primary" class="my-4" @click.native="handleAddProgram()">Confirm</base-button>
                                          </div>
                                      </form>
                                  </template>
                              </card>
                          </modal>
                        </div>
                        <el-table :data="queriedData">
                            <el-table-column
                                v-for="column in tableColumns"
                                :key="column.label"
                                :min-width="column.minWidth"
                                :prop="column.prop"
                                :label="column.label"
                            >
                            </el-table-column>
                            <el-table-column :min-width="135" align="right" label="Active">
                                <template slot-scope="props">
                                  <base-button
                                    @click.native="handleActivation(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-badge"></i>
                                  </base-button>
                                  <base-button
                                    @click="handleEdit(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-pencil"></i>
                                  </base-button>
                                  <modal :show.sync="modals.editModal">
                                    <template slot="header">
                                        <h5 class="modal-title"> {{ selectedProgram.programName }}</h5>
                                    </template>
                                      <base-input alternative
                                                  class="mb-3"
                                                  v-model="selectedProgram.programName"
                                                  placeholder="Program Name"
                                                  addon-left-icon="ni ni-email-83">
                                      </base-input>
                                      <base-input alternative
                                                  type="textarea"
                                                  v-model="selectedProgram.terms_conditions"
                                                  placeholder="Terms and Condition"
                                                  addon-left-icon="ni ni-lock-circle-open">
                                      </base-input>
                                      <div>
                                        <image-upload @change="onImageChange" select-text="Select Image" />  
                                      </div>
                                    <template slot="footer">
                                        <base-button type="secondary" @click="modals.editModal = false">Close</base-button>
                                        <base-button type="primary" @click.native="editMessage(selectedProgram)">Save changes</base-button>
                                    </template>
                                  </modal>
                                  <base-button
                                    @click.native="handleDelete(props.$index, props.row)"
                                    class="edit btn-link"
                                    type="warning"
                                    size="sm"
                                    icon
                                  >
                                    <i class="tim-icons icon-trash-simple"></i>
                                  </base-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </div>  
                    <div
                        slot="footer"
                        class="col-12 d-flex justify-content-center justify-content-sm-between flex-wrap"
                    >
                        <div>
                            <p class="card-category">
                                Showing {{ from + 1 }} to {{ to }} of {{ total }} entries
                            </p>
                        </div>
                        <base-pagination
                            class="pagination-no-border"
                            v-model="pagination.currentPage"
                            :per-page="pagination.perPage"
                            :total="total"
                        >
                        </base-pagination>
                    </div>          
                </card>
            </div>
        </div>
    </div>
</template>
<script>
  import { Table, TableColumn, Select, Option } from 'element-ui'
  import { BasePagination, BaseSwitch, Modal, ImageUpload } from 'src/components'
  import programs from './programsList'
  import Fuse from 'fuse.js'
  import swal from 'sweetalert2'

  export default {
    name: 'Programs',
    components: {
      BasePagination,
      [Select.name]: Select,
      [Option.name]: Option,
      [Table.name]: Table,
      [TableColumn.name]: TableColumn,
      BaseSwitch,
      Modal,
      ImageUpload
    },
    computed: {
      /***
       * Returns a page from the searched data or the whole data. Search is performed in the watch section below
       */
      queriedData () {
        let result = this.tableData
        if (this.searchedData.length > 0) {
          result = this.searchedData
        }
        return result.slice(this.from, this.to)
      },
      to () {
        let highBound = this.from + this.pagination.perPage
        if (this.total < highBound) {
          highBound = this.total
        }
        return highBound
      },
      from () {
        return this.pagination.perPage * (this.pagination.currentPage - 1)
      },
      total() {
        return this.searchedData.length > 0
          ? this.searchedData.length
          : this.tableData.length
      }
    },
    data() {
      return {
        pagination: {
          perPage: 5,
          currentPage: 1,
          perPageOptions: [5, 10, 25, 50],
          total: 0
        },
        modals: {
            modal1: false,
            editModal: false
        },
        selects: {
            types:[
               {value: '1', label: 'Rewards Points'},
               {value: '2', label: 'Promotions'},
               {value: '3', label: 'Coupons'},
               {value: '4', label: 'Tickets'}]
        },
        searchQuery: '',
        propsToSearch: ['programID', 'programName', 'start_date', 'end_date', 'merchantId'],
        tableColumns: [
          {
            prop: 'programID',
            label: 'Program ID',
            minWidth: 100
          },
          {
            prop: 'merchantId',
            label: 'merchant ID',
            minWidth: 120
          },
          {
            prop: 'programName',
            label: 'Name',
            minWidth: 250
          }
        ],
        selectedProgram: '',
        tableData: programs,
        searchedData: [],
        fuseSearch: null,
        programTypeSelecion: null,
        actionStatus: null,
        images: {
          regular: null
        }
      }
    },
    methods: {
      handleEdit(index, row) {
          this.modals.editModal = true;
          this.selectedProgram = row;
        },
        editMessage(row){
          this.actionStatus = 'edit';
          this.modals.editModal = false;
          swal({
                title: `You Edit ` + row.programName + ` successfully`,
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-info btn-fill'
            })
        },
        handleAddProgram() {
          this.actionStatus = 'add';
            this.modals.modal1 = false
            swal({
                title: `You Add it successfully`,
                buttonsStyling: false,
                confirmButtonClass: 'btn btn-info btn-fill'
            })
        },
      handleActivation(index, row) {
        
        if (row.active == true) {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            confirmButtonText: 'Yes, Deactivate it!',
            buttonsStyling: false
          }).then(result => {
            if (result.value) {
              swal({
                title: 'Deactivated!',
                text: `You Deactivated ${row.programName}`,
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
          })
        } else {
          swal({
            title: 'Are you sure?',
            type: 'warning',
            showCancelButton: true,
            confirmButtonClass: 'btn btn-success btn-fill',
            cancelButtonClass: 'btn btn-danger btn-fill',
            confirmButtonText: 'Yes, Activate it!',
            buttonsStyling: false
          }).then(result => {
            if (result.value) {
              swal({
                title: 'Activated!',
                text: `You Activated ${row.programName}`,
                type: 'success',
                confirmButtonClass: 'btn btn-success btn-fill',
                buttonsStyling: false
              })
            }
          })
        }
      },
      handleApprove(index, row) {
        swal({
          title: 'Are you sure?',
          text: `You won't be able to revert this!`,
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, Approve it!',
          buttonsStyling: false
        }).then(result => {
          if (result.value) {
            this.deleteRow(row)
            swal({
              title: 'Approved!',
              text: `You Approved ${row.programName}`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            })
          }
        })
      },
      handleDelete(index, row) {
        swal({
          title: 'Are you sure?',
          text: `You won't be able to revert this!`,
          type: 'warning',
          showCancelButton: true,
          confirmButtonClass: 'btn btn-success btn-fill',
          cancelButtonClass: 'btn btn-danger btn-fill',
          confirmButtonText: 'Yes, delete it!',
          buttonsStyling: false
        }).then(result => {
          if (result.value) {
            this.deleteRow(row)
            swal({
              title: 'Deleted!',
              text: `You deleted ${row.programName}`,
              type: 'success',
              confirmButtonClass: 'btn btn-success btn-fill',
              buttonsStyling: false
            })
          }
        })
      },
      deleteRow(row) {
        let indexToDelete = this.tableData.findIndex(
          tableRow => tableRow.id === row.id
        )
        if (indexToDelete >= 0) {
          this.tableData.splice(indexToDelete, 1)
        }
      },
      onImageChange(file) {
        this.images.regular = file;
      }
    },
    mounted() {
      // Fuse search initialization.
      this.fuseSearch = new Fuse(this.tableData, {
        keys: ['brandName', 'phone', 'email', 'merchantID'],
        threshold: 0.3
      })
    },
    watch: {
      /**
       * Searches through the table data by a given query.
       * NOTE: If you have a lot of data, it's recommended to do the search on the Server Side and only display the results here.
       * @param value of the query
       */
      searchQuery(value) {
        let result = this.tableData
        if (value !== '') {
          result = this.fuseSearch.search(this.searchQuery)
        }
        this.searchedData = result
      },
      programTypeChange(value) {
        this.programTypeSelecion = value
      }
    }
  }
</script>
<style>
  .pagination-select,
  .search-input {
    width: 200px;
  }
</style>