<template>
  <div class="row justify-content-center">
    <div class="col-sm-12">
      <h4 class="info-text">Bank Details</h4>
    </div>
    <div class="col-sm-3">
      <base-input
        label="Bank name"
        name="Bank name"
        v-model="model.bankName"
        :error="getError('Bank name')"
        v-validate="modelValidations.bankName"
      >
      </base-input>
    </div>
    <div class="col-sm-3">
      <base-input
        label="Beneficiary name"
        name="Beneficiary name"
        v-model="model.benName"
        :error="getError('Beneficiary name')"
        v-validate="modelValidations.benName"
      >
      </base-input>
    </div>
    <div class="col-sm-5">
      <base-input
        label="Account number"
        name="Account number"
        v-model="model.accountNumber"
        :error="getError('Account number')"
        v-validate="modelValidations.accountNumber"
      >
      </base-input>
    </div>
    <div class="col-sm-3">
      <base-input
        label="Branch code"
        name="Branch code"
        v-model="model.branchCode"
        :error="getError('Branch code')"
        v-validate="modelValidations.branchCode"
      >
      </base-input>
    </div>
    <div class="col-sm-3">
      <base-input
        label="Swift code"
        name="Swift code"
        v-model="model.swiftCode"
        :error="getError('Swift code')"
        v-validate="modelValidations.swiftCode"
      >
      </base-input>
    </div>
    <div class="col-sm-3">
      <base-input
        label="Address"
        name="Address"
        v-model="model.address"
        :error="getError('Address')"
        v-validate="modelValidations.address"
      >
      </base-input>
    </div>
  </div>
</template>
<script>
import { Select, Option } from 'element-ui'

export default {
  components: {
    [Select.name]: Select,
    [Option.name]: Option
  },
  data() {
    return {
      model: {
        bankName: '',
        benName: '',
        accountNumber: '',
        swiftCode: '',
        branchCode: '',
        address: ''
      },
      modelValidations: {
        bankName: {
          required: true,
          min: 5
        },
        benName: {
          required: true,
          min: 5
        },
        accountNumber: {
          required: true
        },
        swiftCode: {
          required: true
        },
        branchCode: {
          required: true
        },
        address: {
          required: true
        }
      }
    }
  },
  methods: {
    getError(fieldName) {
      return this.errors.first(fieldName)
    },
    validate() {
      return this.$validator.validateAll().then(res => {
        this.$emit('on-validated', res, this.model)
        return res
      })
    }
  }
}
</script>
<style></style>
